.main-view {
    ul, ol {
        padding-left: 2rem;
    }

    .feedback-slide {
        width: 100%;
        height: 100%;
        padding: 0 2rem;
        display: flex;
        align-items: center;
        font-size: 1.2rem;
        justify-content: center;
        position: relative;

        .feedback-block {
            padding: 0 2rem;
            flex-basis: 50%;
        }

        .examine-action-hint {
            position: absolute;
            left: 50%;
            top: 0;
            transform: translateX(-50%);
        }

        // First pane
        .feedback-prompt {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 1rem;

            .rating-btns {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 1rem;

                button {
                    &:hover {
                        background-color: #eee;
                    }
                }
            }
        }

        // Middle Feedback pane doesn't need extra styling... yet?

        // Examine pane
        .feedback-form {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 1rem;
            text-align: center;

            textarea {
                width: 80%;
                padding: 2px 3px;
            }
        }
    }
}